import React from 'react';
import {Row, Col,Card,Icon,Button,Pagination} from 'antd';
import styles from '../MainCases/MainCases.less';


function ActivitiesList(props) {
  const {listTitle,listContent,pagination, onPageChange} = props;
  return (
    <div className={styles.normalCases}>
      <div className={styles.serviceDiv}></div>
      <hr className={styles.serviceHr}/>
      <Row gutter={16}>
      {listTitle && listTitle.length > 0? listTitle.map((data,index) =>{
        const {id, articleTitle, articleCover, author, articleContent, createdAt} = data;
        return(
          <Col  lg={24} span={24} style={{height: '330px'}}>
            <a  href={'/activity/'+id} key={index} style={{color: 'gray', textDecorator: ''}}>
              <div className={styles.activitieslistTitle}>
                <div className={styles.activitiesTitleImg}>
                  <img  src={articleCover} alt="" width='100%' height='100%'/>
                </div>
                <div className={styles.activitiesTitleContent}>
                  <div className={styles.productName}>
                    <p className={styles.ActivityTitle}>{articleTitle}</p>
                    <div className={styles.Activitycontent} dangerouslySetInnerHTML={{__html: articleContent}}/>
                    <div style={{marginTop:'70px',color:'#666666'}}>
                      {createdAt}
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </Col>
        )
      }) : ''}
      </Row>
      <Row gutter={16}>
        {listContent && listContent.length > 0? listContent.map((data,index) =>{
          const {id, articleTitle, articleCover, author, articleContent, createdAt} = data;
          return(
            <Col  lg={8} span={12} style={{height: '330px'}}>
              <a  href={'/activity/'+id} key={index} style={{color: 'gray', textDecorator: ''}}>
                <Card bordered={false}>
                  <img src={articleCover}  width='268px' height='178px'/>
                  <div className={styles.productName}>
                    <p className={styles.casesTitle}>{articleTitle}</p>
                    <div className={styles.casescecontent} dangerouslySetInnerHTML={{__html: articleContent}}/>
                    <div style={{marginTop:'6px',color:'#0496ff'}}>
                      {createdAt}
                    </div>
                  </div>
                </Card>
              </a>
            </Col>
          )
        }) : ''}
      </Row>
      <Row className={styles.footerpage}>
        {pagination && pagination.total > 1 ?
          <Pagination defaultPageSize={9} defaultCurrent={pagination.current} size="large" total={pagination.total} style={{float:'right'}} onChange={onPageChange}/>
          :''}
      </Row>
    </div>
  );
}

export default ActivitiesList;



