import React from 'react';
import { connect } from 'dva';
import styles from './Activities.css';
import ActivitiesList from '../components/MainActivity/activitiesList';


function Activities({dispatch, activities}) {
  const onPageChange = function(page,pageSize){
    dispatch({
      type: 'activities/query',
      payload:{
        page: page,
        pageSize: pageSize
      }
    })
  }
  return (
    <div style={{marginBottom:'-5px'}}>
      <ActivitiesList {...activities} onPageChange={onPageChange} />
    </div>
  );
}

function mapStateToProps({activities}) {
  return {activities};
}

export default connect(mapStateToProps)(Activities);



